<template>
  <div class="outContainer">
    <el-form :rules="rules" ref="loginForm" :model="loginForm" class="loginContainer" v-loading="loading">
      <h3 class="loginTitle">系统登陆</h3>

      <el-form-item prop="username">
        <el-input size="normal" type="text" v-model="loginForm.username" auto-complete="off" placeholder="请输入用户名"></el-input>
      </el-form-item>

      <el-form-item prop="password">
        <el-input size="normal" type="password" v-model="loginForm.password" auto-complete="off" placeholder="请输入密码"
         ></el-input>
      </el-form-item>

      <el-form-item prop="code">
        <el-input size="normal" type="text" v-model="loginForm.code" auto-complete="off" placeholder="点击图片更换验证码"
                  @keydown.enter.native="submitLogin" style="width: 250px;"></el-input>
        <img :src="vcUrl" alt="" @click="updateCode">
      </el-form-item>

      <el-checkbox size="normal" v-model="checked" class="loginRemember">记住密码</el-checkbox>

      <el-button size="normal" type="primary" style="width:100%" @click="submitLogin">登录</el-button>
    </el-form>
  </div>
</template>

<script>

// import {postKeyValueRequest} from "../utils/api";

export default {
  name: "Login",
  data() {
    return {
      vcUrl:'/verifyCode?time='+new Date(),
      loginForm: {
        username:'admin',
        password: '123',
        code:''
      },
      checked:true,
      rules: {
        username: [{required:true,message:'请输入用户名',trigger:'blur'}],
        password: [{required:true,message:'请输入密码',trigger:'blur'}],
        code: [{required:true,message:'请输入验证码',trigger:'blur'}]
      },
      loading:false
    }
  },
  methods:{
    updateCode(){
      this.vcUrl = '/verifyCode?time='+new Date();
    },
    submitLogin(){
      this.$refs.loginForm.validate((valid) => {
          if (valid){
            this.loading = true;
          //  验证成功，发送网络请求
            this.postRequest("/doLogin",this.loginForm).then(resp=>{
              this.loading = false;
              if (resp){
                // 如果能到这里，代表成功了。失败已经在postKeyValueRequest中进行处理了。
                // alert(JSON.stringify(resp));
                window.sessionStorage.setItem("user",JSON.stringify(resp.obj));
                let path = this.$route.query.redirect;
                this.$router.replace((path == "/" || path == undefined)? '/home':path);
              }else{
                this.vcUrl = '/verifyCode?time='+new Date();
              }
            })
          }else {
            this.$message.error('请输入所有字段');
            return false;
          }
      });
    }
  }
}
</script>

<style>

  .el-form-item__content{
    display: flex;
    align-items: center;
  }
  .loginContainer{
    border-radius: 15px;
    background-clip: padding-box;
    margin: 18px auto;
    width: 350px;
    padding: 15px 35px 15px 35px;
    background: #fff;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;
  }

  .loginTitle{
    margin: 15px auto 20px auto;
    text-align: center;
    color: #505458;
  }

  .loginRemember{
    text-align: left;
    margin: 0px 0px 15px 0px;
  }

  .outContainer{
    margin-top: 200px;
  }
</style>